import React, { useState, useRef, useReducer } from "react";
import * as mobilenet from "@tensorflow-models/mobilenet";
import "./App.css";
import Button from "@material-ui/core/Button";
import { makeStyles } from "@material-ui/core/styles";
import Header from "./Header.js";
import styled, { keyframes } from "styled-components";
import * as tf from '@tensorflow/tfjs';

const useStyles = makeStyles({
  form: {
    display: "flex",
    flexDirection: "column",
    lineHeight: 1.5,
    boxSizing: "border-box",
    fontSize: "16px",
    marginLeft: "40%",
    marginRight: "40%",
    borderWidth: "1",
    borderColor: "#e66465",
    border: "solid",

    alignItems: "center",
    justifyContent: "center",
  },
  formItem: {
    display: "flex",
    flexDirection: "column",
    marginBottom: 2,
  },
  formInput: {
    alignSelf: "flex-start",

    padding: "0.8em",
    fontSize: "0.9em",
    fontFamily: '"Source Sans Pro", sans-serif',

    outline: "none",
    border: "1px solid #dddddd",
    borderRadius: "4px",
    background: "#f9f9f9",
  },
  formLabel: {
    fontWeight: 600,
    padding: "10px 0",
    alignSelf: "flex-start",
  },
  button: {
    fontSize: "20px",
    color: "white",
    textTransform: "none",
  },
});

const machine = {
  initial: "initial",
  states: {
    initial: { on: { next: "loadingModel" } },
    loadingModel: { on: { next: "modelReady" } },
    modelReady: { on: { next: "imageReady" } },
    imageReady: { on: { next: "identifying" }, showImage: true },
    identifying: { on: { next: "complete" } },
    complete: {
      on: { next: "modelReady" },
      showImage: true,
      showResults: true,
    },
  },
};

const Container = styled.div`
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
  padding: 30px;
`;

function App() {
  const classes = useStyles();
  const [results, setResults] = useState([]);
  const [imageURL, setImageURL] = useState(null);
  const [model, setModel] = useState(null);
  const imageRef = useRef();
  const inputRef = useRef();

  const reducer = (state, event) =>
    machine.states[state].on[event] || machine.initial;

  const [appState, dispatch] = useReducer(reducer, machine.initial);
  const next = () => dispatch("next");

  const loadModel = async () => {
    next();
    const model = await tf.loadLayersModel('model/model.json')
    setModel(model);
    next();
  };

  const identify = async () => {
    next();
    const results = await model.classify(imageRef.current);
    setResults(results);
    next();
  };

  const reset = async () => {
    setResults([]);
    next();
  };

  const upload = () => inputRef.current.click();

  const handleUpload = (event) => {
    const { files } = event.target;
    if (files.length > 0) {
      const url = URL.createObjectURL(event.target.files[0]);
      setImageURL(url);
      next();
    }
  };

  const actionButton = {
    initial: { action: loadModel, text: "Load Model" },
    loadingModel: { text: "Loading Model..." },
    modelReady: { action: upload, text: "Upload Image" },
    imageReady: { action: identify, text: "Identify" },
    identifying: { text: "Identifying..." },
    complete: { action: reset, text: "Reset" },
  };

  const { showImage, showResults } = machine.states[appState];

  return (
    <div>
      <Header></Header>
      <Container>
        {showImage && (
          <img src={imageURL} alt="upload-preview" ref={imageRef} />
        )}
        <input
          type="file"
          accept="image/*"
          capture="camera"
          onChange={handleUpload}
          ref={inputRef}
        />
        {showResults && (
          <ul>
            {results.map(({ className, probability }) => (
              <li key={className}>{`${className}: %${(
                probability * 100
              ).toFixed(2)}`}</li>
            ))}
          </ul>
        )}
        <Button
          className={classes.button}
          variant="contained"
          color="secondary"
          onClick={actionButton[appState].action || (() => {})}
        >
          {actionButton[appState].text}
        </Button>
      </Container>
    </div>
  );
}

export default App;
